.ProjectPreview {
  text-decoration: none;
  color: #000;

}

@-webkit-keyframes fade-in-bottom {
  from {
    opacity: 0;
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
  }
  to {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes fade-in-bottom {
  from {
    opacity: 0;
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
  }
  to {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

.ProjectPreview {
  opacity: 0;
  -webkit-animation: fade-in-bottom ease 1s forwards;
          animation: fade-in-bottom ease 1s forwards;
}

.ProjectPreview .inner {
  background: #fff;
  padding: 7% 12%;
}

.ProjectPreview .image{
  width: auto;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  margin-top: 20px;
}

.ProjectPreview img{
  max-width: 100%;
  max-height: 100%;
}

.ProjectPreview img:hover{
  -webkit-transform: scale(1.03);
      -ms-transform: scale(1.03);
          transform: scale(1.03);
}

@media (max-width: 620px){
  .ProjectPreview .inner {
    margin-bottom: 15px;
  }

}

@media (min-width:621px) and (max-width:800px){
  .ProjectPreview .inner {
    margin-bottom: 5%;
  }

}

@media (min-width: 801px){
  .ProjectPreview .inner {
    -ms-flex-positive: 1;
        flex-grow: 1;
  }

  .ProjectPreview{
    width: 30%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    margin-bottom: 5%;
  }

}



